import { z } from "zod"

export const FormSchema = z.object({
    email: z.string().email(" ").min(1, "    "),
    username: z.string().min(1, "    ").max(16),
    surname: z.string().min(1, "    ").max(28),
    password: z
      .string()
      .min(1, "    ")
      .min(8, "    8 "),
    confirmPassword: z.string().min(1, "    "),
  })

export type FormSchemaType = z.infer<typeof FormSchema>